:host {
  display: block;
  font-family: 'Prelo';
  border: 1px solid #eee;
  padding: 4px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  padding: 12px;

  > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .pages {
    margin-right: auto;
    border: 1px solid #E9ECF3;

    &, a {
      border-radius: 2px;
    }
  }

  .counts {
    margin-right: 18px;
    color: #000000;
    font-weight: 500;
  }

  .size {
    margin-right: 4px;
  }

  a {
    cursor: pointer;
    text-decoration: none;
    display: inline-block;
    padding: 2px 8px;
    border: 1px solid #fafafa;
    margin-right: 4px;
    transition: all 0.1s linear;
    user-select: none;
    color: #0F265C;
    font-weight: 500;

    &:hover {
      border-color: #aaa;
    }

    &.active {
      color: #ffffff;
      border-color: #0F265C;
      background-color: #0F265C;
    }

    &.disabled {
      cursor: default;
      color: #6F7D9D;

      &:hover {
        border-color: #fafafa;
      }
    }
  }
}
